import React from 'react'
import Standalone from '../layouts/standalone'
import SEO from '../seo'
import { graphql } from 'gatsby'
import moment from 'moment'
import Share from '../share'
import Disqus from 'disqus-react'
import Pick from '../pick'

const BlogPost = ({ data }) => {
  const { commonKeywords } = data.site.siteMetadata
  const content = data.markdownRemark.html
  const { title, description, date, image, author } = data.markdownRemark.frontmatter
  return (<Standalone>
    <SEO
      title={`Expatty Blog | ${title}`}
      description={description}
      keywords={[title, ...commonKeywords]}/>


    <div className="container-fluid blog">
      <div className="row mt-5">
        <div className="col-md blog-image">
          <img className="img-fluid" src={image} alt={title}/>
        </div>
      </div>
      <div className="row">
        <div className="col-md-8">
          <div className="row">
            <div className="col-md">
              <main>
                <article className="mt-5" itemScope itemType="http://schema.org/BlogPosting">

                  <header>
                    <h1 className="title--header" itemProp="name headline">{title}</h1>
                    <p>
                      <time className="light-primary-colour pt-2" dateTime={moment(date)} itemProp="datePublished">
                        {moment(date).format('dddd, D MMMM YYYY')}
                      </time>
                      <span itemProp="author" itemScope itemType="http://schema.org/Person">  â€¢ <span
                        className="light-primary-colour" itemProp="name">{author}</span>
                      </span>
                    </p>
                  </header>

                  <div itemProp="articleBody" dangerouslySetInnerHTML={{ __html: content }}/>
                  <a href={data.markdownRemark.fields.slug} hidden> </a>
                </article>
              </main>
            </div>
          </div>
          <div className="row">
            <div className="col-md">
              <Share/>
            </div>
          </div>
          <div className="row">
            <div className="col-md">
              <Disqus.DiscussionEmbed shortname="expatty"/>
            </div>
          </div>
        </div>
        <div className="col-md-4 blog-picks">
          <h2 className="title--header small mt-5">Patty's Picks</h2>
          {
            data.picks.edges.map(({ node }, idx) => {
              const listingIcons = node.frontmatter.tags
                .map(tg => data.categories.edges.map(edge => edge.node).find(catNode => catNode.frontmatter.tag === tg))
                .filter(catNode => catNode)
                .map(catNode => ({
                  icon: catNode.frontmatter.icon,
                  name: catNode.frontmatter.name,
                  path: catNode.fields.slug
                }));
              return (
                <Pick
                  key={ idx }
                  title={ node.frontmatter.title }
                  url={ node.fields.slug }
                  icons={ listingIcons }
                  description={ node.frontmatter.description }
                  content={ node.html }
                  image={ node.frontmatter.image }/>
              );
            })
          }
        </div>
      </div>
    </div>


  </Standalone>)
}

export default BlogPost;

export const pageQuery = graphql`
    query Blog($slug: String!) {
        site {
            siteMetadata {
                commonKeywords
            }
        }
        markdownRemark(fields: { slug: { eq: $slug } }) {
            html
            fields {
                slug
            }
            frontmatter {
                title
                description
                date
                image
                author
            }
        }
        picks: allMarkdownRemark(filter: {frontmatter: {pick: {eq: true}}}) {
            edges {
                node {
                    fields {
                        slug
                    }
                    frontmatter {
                        title
                        description
                        image
                        tags
                    }
                    html
                }
            }
        }
        categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
    }
`
